import React, { ReactElement, useState, useEffect } from 'react';
import 'react-virtualized/styles.css';
import { useHistory } from 'react-router-dom';
import { Column, Table, AutoSizer } from 'react-virtualized';

import type { Evidence } from '@jan6evidence/types';

import { ResultLocation, ResultSummary, ResultTags, ResultThumbnail, ResultTime } from './Result';

import { TableWrapper } from './evidence-results-styled-components';

type Props = {
  results: Evidence[];
  activeResult: Evidence | null;
  setActiveResult: (result: Evidence | null) => void;
  scrollToResult: Evidence | null;
};

function cellDataGetter({ rowData }: { rowData: any }): any {
  // Be default, react-virtualized tries to pass the cell's render
  // function just the data for that cell. To make our cell renderers
  // simpler, we pass the full row of data (so we can easily have cells
  // that show multiple fields of data)
  return rowData;
}

function makeCellRenderer(
  Component: React.ComponentType<{ cellData: Evidence }>
): ({ cellData }: { cellData?: Evidence }) => ReactElement {
  return function inner({ cellData }) {
    if (!cellData) {
      return <span />;
    }

    return <Component cellData={cellData} />;
  };
}

const ROW_HEIGHT = 150;

const EvidenceTable = ({ results, activeResult, setActiveResult, scrollToResult }: Props) => {
  const history = useHistory();
  const handleOnClick = React.useCallback(
    ({ index }) => {
      setActiveResult(results[index]);
      history.push(`/evidence/detail/${results[index]._id}`);
    },
    [history, results]
  );
  const [scrollTop, setScrollTop] = useState(0);

  useEffect(() => {
    if (scrollToResult) {
      const index = results.findIndex((r) => r === scrollToResult);
      setScrollTop(index * ROW_HEIGHT);
    }
  }, [scrollToResult]);

  return (
    <TableWrapper>
      <AutoSizer>
        {({ height, width }) => (
          <Table
            height={height}
            width={width}
            headerHeight={40}
            rowHeight={ROW_HEIGHT}
            rowCount={results.length}
            rowGetter={({ index }) => results[index]}
            rowClassName={({ index }) =>
              results[index] === activeResult ? 'table-row active' : 'table-row'
            }
            onRowClick={handleOnClick}
            scrollTop={scrollTop}
            onScroll={({ scrollTop: newScrollTop }: { scrollTop: number }) =>
              setScrollTop(newScrollTop)
            }
          >
            <Column
              label="Thumbnail"
              dataKey="thumbnail"
              cellDataGetter={cellDataGetter}
              cellRenderer={makeCellRenderer(ResultThumbnail)}
              width={180}
            />
            <Column
              label="Summary"
              dataKey="summary"
              cellDataGetter={cellDataGetter}
              cellRenderer={makeCellRenderer(ResultSummary)}
              width={360}
              flexGrow={1}
            />
            <Column
              label="Tags"
              dataKey="tags"
              cellDataGetter={cellDataGetter}
              cellRenderer={makeCellRenderer(ResultTags)}
              width={100}
            />
            <Column
              label="Time"
              dataKey="time"
              cellDataGetter={cellDataGetter}
              cellRenderer={makeCellRenderer(ResultTime)}
              width={80}
            />
            <Column
              label="Location"
              dataKey="location"
              cellDataGetter={cellDataGetter}
              cellRenderer={makeCellRenderer(ResultLocation)}
              width={144}
            />
          </Table>
        )}
      </AutoSizer>
    </TableWrapper>
  );
};

export default EvidenceTable;
